import React from 'react'
import SingleTodo from './Todo';

function Todo({persons, filterHandler}) {
    const deletItem = (id) => {
        filterHandler(id);
    }
    const newPerson = persons.map((person) => {
        return(
            <SingleTodo person={person} deletItem={deletItem} key={person.id}/>
        );
    })
    return(
        [newPerson]
    );
}

export default Todo
