import React, {useState, useEffect} from 'react';
import './index.css';
import TodoForm from './components/TodoForm';
import TodoList from './components/TodoList';
import { v4 as uuidv4 } from 'uuid';


function App() {

  const [persons, setPerson] = useState([]);

  const LOCAL_STORAGE_KEY = "persons";
  const postHandler = (person) => {
    setPerson([...persons,{id: uuidv4(), ...person}]);
  }
  
  const filterHandler = (id) => {
    const newFilter = persons.filter((person) => {
      return person.id !== id;
    });
    setPerson(newFilter);
  }
  useEffect(() => {
    const StorageHandler = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY))
    if(StorageHandler){
      setPerson(StorageHandler);
    }
  },[])

  useEffect(() => {
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(persons))
  },[persons])

  return (
    <div className="container">
      <TodoForm postHandler={postHandler}/>
      <TodoList persons = {persons} filterHandler={filterHandler}/>
    </div>
  );
}

export default App;
