import React from 'react'

function Todo(props) {
    const {id, name, email} = props.person;
    return (
        <div className="todo_list">
            <div className="detiles">
                <span>{name}</span>
                <span>{email}</span>
            </div>
            <span onClick={() => props.deletItem(id)}><i className="trash far fa-trash-alt"></i></span>
        </div>
    )
}

export default Todo
